.history {
  h2 {
    transform: rotate(-90deg);
    color: $color-default-white;
    margin: 2.5% 0 0 0;

    writing-mode: vertical-rl;
    transform: rotate(-180deg);
    color: #ffffff;

    @media (max-width: 600px) {
      display: none;
    }
  }
}

.history__wrapper {
  position: relative;
  display: flex;
}

.history__inner {
  display: flex;
  padding-top: 2.7%;

  @media (max-width: 600px) {
    display: flex;
    flex-direction: column;
    padding-top: 0;
  }

  p {
    text-indent: 32px;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    margin: 0 0 6% 0;

    @media (max-width: 600px) {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: 0px;
      text-align: left;
      margin: 0;
    }
  }
}

.history__info {
  margin-left: 3.1%;
  margin-right: 6.2%;
  width: 31%;

  @media (max-width: 600px) {
    width: 100%;
    margin: 0;
  }
}

.history__details {
  width: 50.7%;

  @media (max-width: 600px) {
    width: 100%;
  }
}

.history__plan {
  height: 318px;
  width: 405px;
  margin-top: 12%;

  @media (max-width: 600px) {
    display: none;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.history__video {
  height: 485px;
  width: 100%;

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }

  @media (max-width: 600px) {
    // height: 201px;
    position: relative;
    height: auto;
    margin-top: 20px;

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      min-height: 201px;
    }
  }
}

.history__texts {
  margin-top: 14px;

  p {
    text-indent: 0;
  }
}

.history__house-text {
  h3 {
    margin: 20px 0 30px;
  }
  p {
    text-indent: 0;

    &:first-of-type {
      margin-bottom: 70px;
    }
  }
}
