.theater__inner {
  margin-left: 28%;
  margin-top: 2.5%;
  width: 72%;

  @media (max-width: 600px) {
    width: 100%;
    margin-top: 0;
    margin-left: 0;
  }

  h3 {
    @media (max-width: 600px) {
      margin-bottom: 25px;
    }
  }
}

.theater__texts {
  p {
    text-indent: 32px;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    margin: 0 0 30px 0;

    @media (max-width: 600px) {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: 0px;
      text-align: left;
      text-indent: 0;
      margin-bottom: 22px;

      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}

.theater__pictures {
  position: relative;
  width: 46%;
}

.theater__main-info {
  display: flex;

  @media (max-width: 600px) {
    flex-direction: column;
  }
}

.theater__performance {
  width: 280px;
  height: 360px;
  transform: translateY(-20%);

  @media (max-width: 600px) {
    width: 140px;
    height: 180px;
    margin-left: auto;
    transform: translateY(0);
    width: 140px;
    height: 180px;
    position: absolute;
    top: 90px;
    right: 0;
  }

  img {
    border-radius: 50%;
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.theater__director {
  width: 280px;
  height: 390px;
  position: absolute;

  top: 27%;
  margin-left: 33%;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.theater__text-info {
  width: 48%;
  margin-right: 6%;

  @media (max-width: 600px) {
    width: 100%;
    margin-right: 0;
  }
}

.theater__photo-text {
  font-size: 16px;
  font-style: italic;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0px;
  text-align: left;
  color: $color-default-white;

  @media (max-width: 600px) {
    display: none;
  }
}

.theater__photo-wrapper {
  display: flex;
  margin-top: 7%;

  @media (max-width: 600px) {
    margin-top: 30px;
  }
}

.theater__photo-info {
  width: 42%;
  margin-right: 4%;
  position: relative;

  @media (max-width: 600px) {
    width: 100%;
    margin-right: 0;
  }

  p {
    color: $color-default-white;
    font-size: 16px;
    font-style: italic;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0px;
    text-align: left;

    @media (max-width: 600px) {
      font-size: 12px;
      font-style: italic;
      font-weight: 400;
      line-height: 16px;
      margin: 0 0 6px 0;
    }
  }
}

.theater__photo-text {
  width: 54%;
  margin-top: 6.5%;
  text-indent: 20px;
}

.theater__scene-photo {
  width: 100%;
  height: 219px;

  @media (max-width: 600px) {
    height: auto;
    width: 85%;

    img {
      min-height: 120px;
    }
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    min-height: 120px;
  }
}
