.introduction__wrapper {
  position: relative;

  h1 {
    margin: 10% 0 0 0;

    @media (max-width: 600px) {
      margin: 41px 0 0 0;
      z-index: 2;
      position: relative;
      width: 327px;
    }

    @media (max-width: 360px) {
      margin: 41px 0 0 0;
      z-index: 2;
      position: relative;
      width: 269px;
    }
  }
}

.introduction__inner {
  display: flex;
  position: relative;
}

.introduction__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

}

.introduction__title {
  width: 846px;

  @media (max-width: 600px) {
    width: auto;
  }
}

.introduction__star {
  position: absolute;
  top: -9%;
  left: 53%;
  width: 180px;
  height: 180px;

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.introduction__star-rhomb {
  width: 140px;
  height: 140px;
  position: absolute;
  right: 2%;
  top: 28%;

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.introduction__scheme {
  height: 304px;
  width: 486px;

  @media (max-width: 600px) {
    height: 145px;
    width: 230px;
    margin-right: auto;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.introduction__note {
  background-color: $color-blue;
  border-radius: 50%;
  width: 288px;
  height: 288px;
  margin-bottom: 10%;

  @media (max-width: 600px) {
    width: 160px;
    height: 160px;
    position: relative;
    /* position: absolute; */
    /* bottom: 6px; */
    margin-right: auto;
    margin-top: 70px;
  }

  p {
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 26px;
    padding: 92px 28px;
    transform: rotate(-8deg);
    text-align: center;
    color: $color-default-white;

    @media (max-width: 600px) {
      font-size: 12px;
      font-style: normal;
      transform: rotate(0);
      padding: 60px 22px;
      text-align: center;
      line-height: 14px;
    }
  }
}

.introduction__pictures {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2.5%;

  @media (max-width: 600px) {
    margin-top: -15px;
  }
}

.introduction__painting {
  height: 488px;
  width: 863px;

  @media (max-width: 600px) {
    height: 246px;
    width: 100%;
    position: absolute;
    bottom: 50px;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}