.popularity {
  @media (max-width: 600px) {
    padding-top: 172px;
    overflow: hidden;
    position: relative;

    &:before {
      position: absolute;
      content: "";
      background-image: url("../img/flower.png");
      width: 199px;
      height: 236px;
      left: 0;
      top: 118px;
      z-index: -1;
    }
  }
}

.popularity__content {
  @media (max-width: 600px) {
    h3 {
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: 0px;
      text-align: left;

      &:before {
        display: none;
      }
    }

    p {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: 0px;
      text-align: left;
      color: $color-default-white;
    }
  }
}

.popularity__posters {
  @media (max-width: 600px) {
    position: relative;
    min-height: 315px;
  }
}

.popularity__poster {
  position: absolute;
  width: 45%;

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }

  @media (max-width: 600px) {
    max-width: 142px;
    &--one {
      top: 100px;
      left: 0;
    }

    &--two {
      top: 30px;
      left: 26%;

      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &--three {
      top: 140px;
      right: 0;
    }
  }
}

.popularity__persons {
  @media (max-width: 600px) {
  }
}

.popularity__text {
  margin: 27px 0 0 0;
}
