.person__name {
  @media (max-width: 600px) {
    font-size: 12px;
    font-style: italic;
    font-weight: 400;
    line-height: 14px;
    letter-spacing: 0px;
    text-align: right;
    color: $color-default-white;
    display: block;
    margin-bottom: 10px;
  }
}

.person {
  @media (max-width: 600px) {
    margin-top: 60px;
    &--odd {
      .person__pic {
        margin-left: auto;
      }
      .person__pic-wrapper {
        position: relative;

        &:before {
          position: absolute;
          content: "";
          background-image: url("../img/star_gold.png");
          width: 200px;
          height: 200px;
          background-repeat: no-repeat;
          // right: 77%;
          // top: 43px;
          top: 50%;
          right: 20%;
          transform: translate(-50%, -50%);
        }

        .person__name {
          text-align: right;
        }
      }
    }

    &--even {
      margin-top: 45px;

      .person__pic-wrapper {
        position: relative;
        &:before {
          position: absolute;
          content: "";
          background-image: url("../img/star_gold_2.png");
          width: 180px;
          height: 180px;
          background-repeat: no-repeat;
          // left: 65%;
          // top: 0;
          top: 30%;
          right: 50%;
          transform: translate(95%, -50%);
        }
      }

      .person__texts {
        margin-top: -44px;
      }

      .person__name {
        text-align: left;
      }
    }

    &--odd:last-of-type {
      margin-top: 98px;

      .person__pic-wrapper {
        &:before {
          position: absolute;
          content: "";
          background-image: url("../img/mirror.png");
          width: 140px;
          height: 195px;
          background-repeat: no-repeat;

          top: 50%;
          left: 32%;
          z-index: 1;
          transform: translate(-50%, -50%);
          // left: -30%;
          // bottom: 12px;
          // top: auto;
        }
      }

      .person__name {
        margin-top: 20px;
        margin-bottom: 5px;
      }
    }
  }
}

.person__pic {
  width: 200px;
  height: 280px;
  position: relative;

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.person__texts {
  @media (max-width: 600px) {
    margin-top: -36px;
    position: relative;

    h3 {
      &:before {
        display: block;
      }
    }
  }
}

.person__text {
  margin: 33px 0 0 0;
}

.person__quote {
  margin: 10px 0 0 0;
  display: block;

  font-size: 14px;
  font-style: italic;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0px;
  text-align: left;
  color: $color-default-white;
}

.person__text-additional {
  margin: 5px 0 0 0;
  position: relative;
}
