.modal__overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(51, 51, 51, 0.3);
  backdrop-filter: blur(4px);
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
}

.epoque__modal {

}

.modal__body {
  background: #FFFFFF;
  box-shadow: 2px 2px 20px 1px;
  overflow-x: auto;
  width: 604px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 3px;

  @media (max-width: $tablets-max) {
    width: 60%;
  }

  @media (max-width: $mobiles-max) {
    min-width: 328px;
    width: 56%;
  }
}