.epoque {
  @media (max-width: 600px) {
    margin-top: 40px;
    margin-bottom: 70px;
  }

  // background-image: url('../img/roof.jpg');
  // background-size: cover;
  // background-repeat: no-repeat;
}

.epoque__inner {
  display: flex;
}

.epoque__texts {
  width: 28%;
  margin-right: 5%;

  @media (max-width: 600px) {
    position: relative;
  }

  h3 {
    @media (max-width: 600px) {
      margin: 20px 0 30px;
    }
  }

  p {
    text-indent: 32px;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    margin: 0;

    @media (max-width: 600px) {
      text-indent: 0;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
    }
  }

  @media (max-width: 600px) {
    width: 100%;
    margin-right: 0;
  }
}

.epoque__picture {
  top: -100px;
  position: absolute;
  width: 100%;
  height: 100vh;
  right: -21.5%;

  @media (max-width: 600px) {
    position: relative;
    top: 0;
    right: 0;
    height: 240px;
    width: 100%;
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

